.item {
  position: relative;
}

.image {
  width: 150px;
  height: 150px;
  margin: 0.1rem 0.25rem;
}

.image-footer {
  display: flex;
  padding-right: 0.3rem;
  background: rgb(0 0 0 / 54%);
  height: 27px;
  position: absolute;
  width: 150px;
  z-index: 10;
  /* background: black; */
  left: 4px;
  bottom: 5px;
  justify-content: flex-end;
}

.rotate-icon,
.expand-icon {
  cursor: pointer;
  /* width: 20px;
  margin-left: 1rem; */
}

.wrapper {
  display: flex;
  position: fixed;
  background: #000000c9;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 13;
  align-items: center;
  justify-content: center;
}

.modal {
  position: relative;
  width: 85%;
  height: 70%;
  /* margin: 0 auto;
  height: auto;
  max-height: 70vh;
  margin-top: calc(100vh - 85vh - 20px); */
  background: #fff;
  border-radius: 4px;
  padding: 10px;
  border: 1px solid #999;
  overflow: auto;
}
.close-btn {
  cursor: pointer;
  position: fixed;
  right: 66px;
  top: 99px;
  background: #883434;
  padding: 5px;
  /* width: 25px;
  height: 25px; */

  border: 1px solid #999;
  font-size: 20px;
}
